import React, {useRef, useState, useEffect} from 'react'
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import { Button } from '@mui/material';
// import { styled } from '@mui/material/styles';

const MyThoughts = () => {
    let thDateRef    = useRef();
    let thoughtRef = useRef();
    let [id, setId]  = useState(Date.now());

    let [ThoughtList,setThoughtList] = useState(
        // InfoList either from localStorage or initialized empty
        (localStorage.getItem("MyThoughts")) 
        ? JSON.parse(localStorage.getItem("MyThoughts")) // this will retrieve data from local storage 
        : []);                                        // empty array if not existing in local storage 

    let handleSubmit = (event) => {
        event.preventDefault();
        let thought = 
                {id:id,
                thDate:thDateRef.current.value,
                thought:thoughtRef.current.value};
        console.log(thought)
        // check if ID already exists
        // alert(thDate)
        // let filterbyID = InfoList.filter((item)=> {
        //         return item.id = info.id});
        // if (filterbyID.length == 0) {       
                setThoughtList([...ThoughtList, thought]);
            // }
        // else {
        //         ThoughtList.forEach((item, index) => {
        //                 ThoughtList.splice(index, 1, thought)
        //                 setThoughtList(ThoughtList);
        //         })
        // };
        
        // following codes to clear form and assign new ID
        setId(Date.now());      
        thDateRef.current.value= ""
        thoughtRef.current.value = ""
    }

    useEffect (() => {
        localStorage.setItem("MyThoughts",JSON.stringify(ThoughtList));
        alert(JSON.stringify(ThoughtList));
        }, [ThoughtList])


  return (
    <Box
      component="form"
      sx={{
        '& .MuiTextField-root': { m:1 },
        display: 'flex', flexDirection: 'row', maxwidth:'100%'
      }}
    //   noValidate
    //   autoComplete="off"
    //   onSubmit={handleSubmit}
    >
        <div>
            <h4>
                Thoughts for the Day
            </h4>
            <Grid container columnSpacing={1} >
                <Grid item>
                    <TextField 
                        required
                        id="thDate"
                        label="Date"
                        type="date"
                        ref={thDateRef}
                        // defaultValue={(new Date())}
                    />
                </Grid>
                <Grid item>
                    <TextField 
                        required
                        id="thRemarks"
                        label="Remarks"
                        multiline
                        ref={thoughtRef}
                        rows={3}
                        fullWidth
                    />
                </Grid>
            </Grid>
            <Box
                m={1}
                //margin
                display="flex"
                justifyContent="flex-end"
                alignItems="flex-end"
                >
                <Grid item xs>
                    <Button variant="contained" type="submit" onClick={handleSubmit}>Submit</Button>
                </Grid>
            </Box>
        </div>
    </Box>
  )
}

export default MyThoughts