import React from 'react'

export const BulletinBoard = (props) => {
    let vCategory = (props.category == null)
                ?""
                :"Category: " ;
    let vPost = !(props.post == null)
                ?"Post: " 
                :"";
  return (
    <div>
        <h3>{props.id}</h3> 
        <h3>{vCategory}  {props.category}</h3>
        <h3>{vPost} {props.post}</h3>
    </div>
  )
}
