import React, { useEffect, useState } from 'react'
import randomColor from 'randomcolor';

const Hook = () => {
    // counter
    let [count,   setCount ] = useState(0); //returns two values

    // timer
    let [seconds,  setSeconds]  = useState(0);
    let [isActive, setIsActive] = useState(false);

    let toggle = () => {
            setIsActive(!isActive);
        };
    
    let reset = () => {
            setSeconds(0);
            setIsActive(false);
          }

    useEffect(() => {
        // syntax: useEffect()
        // accepts two arguments:
        // 1st = function
        // 2nd = dependencies (optional)
        let  interval = null;
        if (isActive) {
            interval = setInterval(()=>{
                setSeconds(seconds => seconds + 1 )},1000)
            }
        else if (!isActive && seconds !== 0) {
                clearInterval(interval);
                };

        return () => clearInterval(interval); //cleanup function
    }, [isActive,seconds])

    const [username, setUsername] = useState("Hi user!");

    const useGenerateRandomColor = () => {
        const [color,setColor] = useState("")
        const generateColor = () =>{
            setColor(seconds);
        };
        return {color,generateColor};      
    };

    let [color,setColor] = useState("");
    useEffect(() => {
        setColor(randomColor())
        },[seconds]);


    const [itemName,setItemName] = useState("");
    const [items, setItems] = useState([]);
    let addItem = (e) => {
        e.preventDefault();
        setItems([...items,{id:items.length,name:itemName}]);
        setItemName("");
    }

  return (
    <div>
        <h2 style = {{color:`${color}`}}>Hooks Sample</h2>

        {/* Form with display all inputs */}
        <h2>List of Things</h2>
        <form onSubmit={addItem}>
            <label>My Things: </label>
            <input type="text" onChange={e => setItemName(e.target.value)}></input>
        </form>
        {/* display all inputted data */}
        <ul >
            {items.map(arr => 
            (<li key={arr.id}>{arr.name}</li>))}
        </ul>


        <h2>{username}</h2>
        <input type="text" name='username' value={username} 
                onChange={(event) => {setUsername(event.target.value)}}></input>
        <div>
            <h3>Counter</h3>
            <p>You clicked {count} times.</p>
            <button onClick={() => setCount(count+1)}            >Increase</button>
            <button onClick={() => (count>0)?setCount(count-1):0}>Decrease</button>
        </div>

        <div>
            <h3>Timer</h3>
            <p style = {{color:`${color}`}}>{seconds} </p>
            <button onClick={toggle} > {(isActive) ?"Pause":"Start"} </button>
            <button onClick={reset}> Reset  </button>
        </div>
    </div>
  )
}

export default Hook