import React from 'react'

export const DigiClock = () => {
    const textStyle = {color: "pink", fontSize: "14px"};
    let dateNow   = new Date();
    let xhours    = dateNow.getHours();
    let xmin      = dateNow.getMinutes();   
    let xsec      = dateNow.getSeconds();
    let xhr = (xhours>12) ? xhours-12 
             : xhours;
    xhr  = ((xhr<10)?"0":"")  + xhr;
    xmin = ((xmin<10)?"0":"") + xmin;
    xsec = ((xsec<10)?"0":"") + xsec;
    // setTimeout(function(){DigiClock()}, 1000);
    let xTime = xhr + ":" + xmin + ":" 
              + xsec + (xhours<12?" AM":" PM");
  return (
    <div style={textStyle}>{xTime}</div>
  )
}
