import React from "react";

let Greetings = (props) => {
    const textStyle = {color: "yellow", fontSize: "14px"};
    let name = props.firstName +" "+ props.lastName;
    const dateNow = new Date();
    let houroftheDay = dateNow.getHours();
    let greetings;
    greetings = (houroftheDay < 12 ) 
                ? "Good morning"   
                : (houroftheDay>=12 && houroftheDay < 18) 
                ? "Good afternoon"
                : (houroftheDay >= 18)
                ? "Good evening"
                : "Something's wrong with the clock";
    return (
            <div style={textStyle}>
                <b>{greetings} {name}.</b>
            </div>
    );
}

export default Greetings;
