import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Greetings from '../components/Greetings'
import { DigiClock } from '../components/DigiClock';
import {Link} from 'react-router-dom';
import styled from 'styled-components';

const NavList = styled.a`text-decoration:none`;
const StyledLink = styled(Link)
    `
        color:white;
        text-decoration:none;
        margin:1rem;
        position:relative;
        &:hover, &:focus {color:yellow;}
        &:active {color:red;}
    `;

let Header = () => {
    let person = {fName:"Gabriela", lName:"Silang"}
    return (
        <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static">
          <Toolbar>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
            >
            <MenuIcon />
            </IconButton>
            <Typography  component="div" sx={{ display:'flex', gap: 2, flexGrow: 1 }}>
                Company ABC <DigiClock/> <Greetings firstName = {person.fName} lastName = {person.lName}/>
            </Typography>
            <Typography>
                <NavList>
                    <StyledLink to='/'          >Home      </StyledLink>
                    <StyledLink to='/AboutUs'   >About Us  </StyledLink>
                    <StyledLink to='/Activity'  >Activity  </StyledLink>
                    <StyledLink to='/Portfolio' >Portfolio </StyledLink>
                    <StyledLink to='/Hook'      >Hooks     </StyledLink>
                    <StyledLink to='/ChoreForms'>Chores    </StyledLink>
                    <StyledLink to='/ContactUs' >Contact Us</StyledLink>
                    <StyledLink to='/MyJournal' >My Journal</StyledLink>
                    <StyledLink to='/SignUp'    >Sign Up   </StyledLink>
                </NavList>
            </Typography>
            <Button color="inherit">Login</Button>
           </Toolbar>
         </AppBar>
       </Box>
    );
  }

export default Header;