import React, { useEffect, useState } from 'react';
import ChoresList from './ChoresList';

const ChoreForms = () => {
  const [choreName,setChoreName] = useState("");
  const [choreDesc,setChoreDesc] = useState("");
  const [choreDate,setChoreDate] = useState("");
  let choreList = (localStorage.getItem("chores")) 
                 ? JSON.parse(localStorage.getItem("chores")) // this will retrieve data from local storage 
                 : [];                                        // if not existing in local storage 
  const [choreData,setChoreData] = useState(choreList);                                       

  const handleSubmit = (e) => {
    e.preventDefault();
    setChoreData([...choreData, 
                {id:choreData.length,
                 name:choreName,
                 desc:choreDesc,
                 date:choreDate}]);
    }

    useEffect (() => {
        localStorage.setItem("chores",JSON.stringify(choreData));
        }, [choreData])
    

  return (
    <form onSubmit= {e=> {handleSubmit(e)}}>
        <h2>Chore List</h2>
        <div>
            <label>Chore Name : </label>
            <input type="text" name="choreName" value={choreName}
                onChange={e => setChoreName(e.target.value) }></input>
        </div>
        <div>
            <label>Description: </label>
            <input type="text" name="choreDesc" value={choreDesc}
                onChange={e => setChoreDesc(e.target.value) }></input>
        </div>
        <div>
            <label>Date: </label>
            <input type="date" name="choreDate" value={choreDate}
                onChange={e => setChoreDate(e.target.value) }></input>
        </div>
        <input type="submit" value="Submit"></input>

        <ChoresList data={choreData}/>

        {/* <ul >
            {choreData.map(arr => 
            (<li key={arr.id}>{arr.name} {arr.desc} {arr.date}</li>))}
        </ul> */}

    </form>
  )
}

export default ChoreForms