// Program ID :  ContactUs.js
// Description:  React js for contact us page for client's use. 
//               all inputs will be stored in localStorage.

import React, { useRef, useState, useEffect } from "react";
import Typography from "@mui/material/Typography";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import ContactList from "../components/ContactList";

const ContactUs = () => {
  // initialization
  const [conFname,   setconFname]  = useState("");
  const [conLname,   setconLname]  = useState("");
  const [conEmail,   setconEmail]  = useState("");
  const [conPhone,   setconPhone]  = useState("");
  const [conMessage, setconMessage]= useState("");
  // for populating table and to make sure stored data will not be overwritten
  // check if existing retrieve otherwise empty array []
  let contactList = (localStorage.getItem("cyberhomies_contactus"))
                  ? JSON.parse(localStorage.getItem("cyberhomies_contactus")) 
                  : [];    
  const [contactData, setContactData] = useState(contactList);

  const handleSubmit = (e) => {
    e.preventDefault(); 
    // transfer entered data to object array
    setContactData([...contactData,
        {id:contactData.length,
        fname:conFname,
        lname:conLname,
        email:conEmail,
        phone:conPhone,
        message:conMessage}]);
    // reset/clear fields
    setconFname('');
    setconLname('');
    setconEmail('');
    setconPhone('');
    setconMessage('');
  }
             
  useEffect(() => {
    localStorage.setItem('cyberhomies_contactus', JSON.stringify(contactData));
  }, [contactData]);

  return (
    <>
      <Card container p={2} size="large">
        <CardContent container p={2}>
          <form onSubmit={e => { handleSubmit(e); } }>
            <Grid container spacing={1} rowSpacing={3} p={2}>
              <Grid xs={12} sm={6} item>
                <TextField
                  label="First Name"
                  placeholder="Enter first name"
                  variant="outlined"
                  fullWidth
                  required
                  autoFocus
                  onChange={e => setconFname(e.target.value)}
                  value={conFname} />
              </Grid>
              <Grid xs={12} sm={6} item>
                <TextField
                  label="Last Name"
                  placeholder="Enter last name"
                  variant="outlined"
                  fullWidth
                  required
                  onChange={e => setconLname(e.target.value)}
                  value={conLname} />
              </Grid>
              <Grid xs={12} item>
                <TextField
                  label="E-mail Address"
                  type="email"
                  placeholder="Enter e-mail address"
                  variant="outlined"
                  fullWidth
                  required
                  onChange={e => setconEmail(e.target.value)}
                  value={conEmail} />
              </Grid>
              <Grid xs={12} item>
                <TextField
                  label="Phone Number"
                  type="tel"
                  placeholder="Enter phone number"
                  variant="outlined"
                  fullWidth
                  required
                  onChange={e => setconPhone(e.target.value)}
                  value={conPhone} />
              </Grid>
              <Grid xs={12} item>
                <TextField
                  label="Message"
                  multiline
                  rows={5}
                  placeholder="Type your message here"
                  variant="outlined"
                  fullWidth
                  required
                  onChange={e => setconMessage(e.target.value)}
                  value={conMessage} />
              </Grid>
              <Grid xs={12} item>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  fullWidth
                >
                  Submit
                </Button>
              </Grid>
            </Grid>
          </form>
        </CardContent>
      </Card>
      <CardContent container p={2}>
        <ContactList data={contactData} />
      </CardContent>
      
    </>
  );
};

export default ContactUs;